#root {
  width: 75vmin;
  height: 75vmin;
  padding: 12.5vmin;
  margin: auto;
}
.grid-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  grid-auto-rows: minmax(33%, 33%);
  grid-auto-columns: minmax(33%, 33%);
  width: 100%;
  height: 100%;
  grid-gap: 1%;
}
.grid-container {
  margin: auto;
  background-color: yellowgreen;
}
.ceil {
  box-sizing: border-box;
  border: 1px solid black;
  width: 100%;
  height: 100%;
  line-height: 100%; 
  text-align: center;
  font-size: 20vmin;
  font-weight: bold;

}
